export default {
	props:[
		'imgUrl'
	],
	data() {
		return {
			//头部滑块
			current:0,
			swiperItemCount:3
		};
	},
	methods:{
		 changeSwiper(e) {
		    this.current=e.detail.current;
		  },
		  cancelCurSwiperIndex(){
			  if(this.current+1==this.swiperItemCount){
				  this.current=0;
				  return;
			  }
			   this.current=this.current+1;
		  }
		  
	}
}


